/* */
.container {
  padding-left: calc(30px / 2);
  padding-right: calc(30px / 2);
  margin: 0 auto;
  box-sizing: border-box;
  width: 100%; }
  @media (min-width: +768px) {
    .container {
      width: 768px; } }
  @media (min-width: +992px) {
    .container {
      width: 984px; } }
  @media (min-width: +1200px) {
    .container {
      width: 1200px; } }

