/*
 * FONTS
 */
/* */
/* CONTAINERS */
/* */
.header__contacts {
  height: 80px;
  display: flex;
  justify-content: space-between; }

/*
 * FONTS
 */
/* */
/* CONTAINERS */
/* */
.header__contacts-item {
  font-size: 24px;
  font-weight: 200;
  color: #151515;
  text-decoration: none; }

/*
 * FONTS
 */
/* */
/* CONTAINERS */
/* */
.services__body {
  display: grid;
  grid-column-gap: 30px;
  grid-template-columns: repeat(2, 1fr); }

/*
 * FONTS
 */
/* */
/* CONTAINERS */
/* */
.services__img {
  background: url("/img/services/img.png") no-repeat center;
  background-size: contain; }

/*
 * FONTS
 */
/* */
/* CONTAINERS */
/* */
.services__item {
  font-size: 20px;
  font-weight: 300;
  letter-spacing: .05em;
  padding: 60px 15px;
  box-shadow: 0 0 20px rgba(0, 0, 0, 0.07);
  border-radius: 5px;
  text-align: center;
  display: flex;
  flex-direction: column;
  align-items: center; }
  .services__item > *:not(:first-child) {
    margin-top: 15px; }

/*
 * FONTS
 */
/* */
/* CONTAINERS */
/* */
.services__item-line {
  width: 70px;
  height: 2px;
  background: #d7d7d7; }

/*
 * FONTS
 */
/* */
/* CONTAINERS */
/* */
.services__list {
  display: grid;
  grid-template-rows: repeat(2, 1fr);
  grid-template-columns: repeat(2, 1fr);
  grid-gap: 30px; }

/*
 * FONTS
 */
/* */
/* CONTAINERS */
/* */
.services__item-title {
  font-size: 20px;
  font-weight: 600;
  text-transform: uppercase;
  position: relative; }
